<template>
    <div class="modal" id="modal">
        <div class="modal-content">
            <div class="modal-header">
                <div class="modal-header-text">
                    Send message
                </div>
                <div @click="closeModal" @mouseenter="isWhite = true" @mouseleave="isWhite = false" class="modal-close">
                    <img style="width:1vw" v-if="!isWhite" src="@/assets/icons/close-black.svg" alt="">
                    <img style="width:1vw" v-else src="@/assets/icons/close-white.svg" alt="">
                </div>
            </div>
            <template v-if="type=='image'">
                <div class="image">
                    <img style="width:30vw;border-radius:0.55vw" :src="'data:'+ image.type+';base64,'+image.b64" alt="">
                </div>
            </template>
            <template v-else-if="type=='new_contact'">
                <input v-mask="'## ### ### ## ##'" spellcheck="false" v-model="number" placeholder="Number" class="number" type="text">
                <textarea spellcheck="false" v-model="message" class="message js-autoresize" placeholder="Message" name="" id="" ></textarea>
            </template>
            <template v-else-if="type==='document'">
                <div class="document">
                    <img style="width:1vw" v-if="image.type === 'application/pdf'" src="@/assets/icons/pdf.svg" alt="">
                    <img style="width:1vw" v-else-if="image.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'" src="@/assets/icons/excel.svg" alt="">
                    <img style="width:1vw" v-else-if="image.type === 'text/plain'" src="@/assets/icons/txt.svg" alt="">
                    <img style="width:1vw" v-else-if="image.type === 'application/x-rar-compressed'" src="@/assets/icons/rar.svg" alt="">
                    <div class="document-text">
                        {{image.b64Name}}
                    </div>
                </div>
            </template>
            <template v-else-if="type==='video'">
                <video style="width:12vw;border-radius:4px" controls v-if="type == 'video'">
                  <source type="video/mp4" :src="'data:'+ image.type+';base64,'+image.b64">
                </video>
            </template>
            <div class="buttons">
                <button @click="closeModal" class="cancel-button">Cancel</button>
                <button @click="sendMessage" class="send-button">Send</button>
            </div>
        </div>
    </div>
</template>

<script>
import {setResizeListeners} from "@/components/chat_page_components/auto-resize.js"
import {genericGetRequests,genericPostRequests} from "@/api/axios.js"
import _ from "lodash"
import moment from "moment"
import {mapMutations} from "vuex"
export default {
    data(){
        return{
            number:"",
            message:"",
            isWhite : false,
            tempMessageHistory:[],
            mainUrl:'http:localhost:8085',
        }
    },
    props:{
        isOpen:{type:Boolean,required:true},
        image:{type:Object},
        messageHistory:{type:Array},
        type:{type:String,required:true},
        selectedChat:{type:String,required:true}
    },
    methods:{
        ...mapMutations([
            "setLogs",
            "setDevTerminal"
        ]),
        async sendMessage(){
            if(this.type == 'image'||this.type==='document'||this.type==='video'){
                //console.log(this.image)
                this.tempMessageHistory = _.cloneDeep(this.messageHistory)
                this.tempMessageHistory.push({
                    mediaKey : "",
                    hasMedia: true,
                    fromMe:true,
                    links:[],
                    type:this.type,
                    body:'',
                    timestamp:moment().unix(),
                    loading:true,
                })
                this.$emit('update:messageHistory',this.tempMessageHistory)
                this.$emit('update:isOpen',false)
                const response = await genericPostRequests(`/send_file/${this.$token}`,this.image)
                if(response.data.status){
                    if(this.type == 'image' || this.type === 'video'){
                        this.tempMessageHistory.splice(this.tempMessageHistory.length-1,1,{
                            mediaKey : 'data:'+ response.data.data.type+';base64,'+response.data.data.b64,
                            hasMedia: true,
                            fromMe:true,
                            links:[],
                            type:this.type,
                            body:'',
                            timestamp:moment().unix(),
                            loading:false,
                        })
                    }
                    else{
                       this.tempMessageHistory.splice(this.tempMessageHistory.length-1,1,{
                            mediaKey : 'data:'+ response.data.data.type+';base64,'+response.data.data.b64,
                            hasMedia: true,
                            fromMe:true,
                            links:[],
                            type:this.type,
                            body:this.image.b64Name,
                            timestamp:moment().unix(),
                            loading:false,
                        }) 
                    }
                    
                    this.$emit('update:messageHistory',this.tempMessageHistory)
                    this.setLogs(response.data)
                    this.setDevTerminal(`curl -H '{'b64':'<Base64>','b64Name':'<File Name>','number':'<Number>', 'type':'<Image Type>' \ -H 'Content-Type: application/json' \ -H 'Authorization':${this.$token}' -X POST ${this.mainUrl}/send_message`)
                }
            }
            else if(this.type == 'new_contact'){
                if(this.number != "" && this.message != ""){
                    const response = await genericPostRequests(`/send_message/${this.$token}`,{
                        number:this.number,
                        message:this.message,
                    })
                    this.setLogs(response.data)
                    this.setDevTerminal(`curl -H '{'number':'${this.number}','message':'${this.message}'} \ -H 'Content-Type: application/json' \ -H 'Authorization':${this.$token}' -X POST ${this.mainUrl}/send_file`)
                    this.$emit('update:messageHistory',[])
                    this.$emit('update:selectedChat',"")
                    this.$emit('update:isOpen',false)
                    
                }
            }
            
        },
        closeModal(){
            this.$emit('update:isOpen',false)
        }
    },
    mounted(){
        setResizeListeners(this.$el, ".js-autoresize");
        //console.log({i:this.image})
    },
    components:{
        setResizeListeners
    },
}
</script>

<style lang="scss">
@import '@/scss/colors.scss';
@import '@/scss/mixins.scss';

.modal{
    @include d-flex(row,center,flex-start);
    position: fixed; /* Stay in place */
    z-index: 999; /* Sit on top */
    padding-top: 6vw; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    .modal-content {
        @include d-flex(column,flex-start,center);
        background-color: #fefefe;
        padding:1vw;
        height: fit-content;
        width: 50vw;
        flex-wrap: nowrap;
        border-radius: 0.5vw;
        .document{
            @include d-flex-center;
            width: 100%;
            gap: 0.5vw;
            .document-text{
                @include d-flex-center;
            }
        }
        .modal-header{
            margin-bottom: 1vw;
            @include d-flex(row,space-between,flex-start);
            border-bottom: 1px solid rgb(124, 124, 124);
            padding-bottom: 0.25vw;
            width: 100%;
            .modal-header-text{
                font-size: 1.25vw;
                font-weight: 700;
            }
            .modal-close{
                @include d-flex-center;
                padding: 0.2vw;
                border-radius: 99px;
                cursor: pointer;
                &:hover{
                    background-color: rgb(172, 172, 172);
                }
            }
        }
        .image{
            @include d-flex(row,center,space-around);
            flex-wrap:wrap;
            margin-bottom:0.75vw;
        }
        .number{
            margin-bottom: 1vw;
            outline: none;
            font-size: 0.95vw;
            padding: 0.2vw;
            border: 1px solid rgb(100, 100, 100);
            border-radius: 0.225vw;
            -webkit-appearance: none;
            width: 100%;
        }
        .message{
            @extend .number;
            resize: initial;
            overflow:hidden;
            resize: none;
        }
        .buttons{
            @include d-flex(row,flex-end,center);
            width: 100%;
            font-size: 0.75vw;
            .send-button{
                @include d-flex-center;
                padding: 0.325vw 0.625vw;
                background-color: rgb(75, 182, 42);
                border-radius: 0.2vw;
                color:#fff;
                &:hover{
                    background-color: rgb(84, 206, 47);
                }
            }
            .cancel-button{
                @extend .send-button;
                background-color: rgb(235, 43, 43);
                margin-right: 0.75vw;
            }
            .cancel-button:hover{
                background-color: rgb(255, 62, 62);
            }
        }
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}









</style>