<template>
    <div class="header">
        <div class="header-icon">
            <img style="width:1.8vw" :src="headerIcon && require('@/assets/icons/'+headerIcon)" alt="">
        </div>
        <div class="header-title">
            {{headerTitle}}
        </div>
    </div>
</template>

<script>
    import {
        icons
    } from "@/helpers/enums"
    export default {
        data() {
            return {
                headerTitle: "",
                headerIcon: "",

            }
        },
        mounted() {
            let path = this.$route.path;
            let parsing_path = path.split("/");
            if (parsing_path[0] === '') {
                parsing_path.shift()
            }
            for (let path of parsing_path) {
                path = '/' + path
                for (let icon of icons) {
                    if (icon['api'] === path) {
                        this.headerTitle = icon.title;
                        this.headerIcon = icon.src;
                    }
                }
            }
        }
    }
</script>

<style lang="scss">
    @import '@/scss/mixins.scss';
    @import '@/scss/colors.scss';

    .header {
        @include d-flex(row, flex-start, center);
        width: 100%;
        background-color: #fff;
        padding: 1.8vmin 1.5vmin;
        color: #003965;
        font-size: 1.5vw;
        gap: .8vw;

        .header-title {
            @include d-flex-center;
        }

        .header-icon {
            @extend .header-title;
        }
    }
</style>