<template>
  <div class="qr-main">
    <div class="qr-card">
      <div class="qr">
        <div class="qr-text">
          <div class="qr-text-header">
            To send and receive messages, you have to authorize our server like for WhatsApp Web
          </div>
          <div class="to-do-list" v-for="(item,i) in qrTexts" :key="'text'+i">
            {{item}}
          </div>
        </div>
        <div class="qr-image">
          <img v-if="qrUrl != ''" class="qr-img" :src="qrUrl" alt="">
          <div id="qr-image" :style="{'height':`${loadingHeight}px`}" v-else class="qr-loading">
              <v-progress-circular
              :size="46"
              :width="5"
              color="#E0E0E5"
              indeterminate
            ></v-progress-circular>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {genericGetRequests,genericPostRequests} from '@/api/axios.js'
import {mapMutations} from "vuex"
export default {
  data(){
    return{
      qrTexts:[
        '1. Open the WhatsApp app on your phone',
        '2. Press Settings and select Connected Devices',
        '3. Scan a code and wait a minute',
        '4. Keep your phone turned on and connected to the Internet',
      ],
      qrUrl:"",
      genericPostRequests,
      loadingHeight : null,
    }
  },
  async mounted(){
    this.loadingHeight = document.getElementById('qr-image').offsetWidth
    window.addEventListener('resize', event =>{
      this.loadingHeight = document.getElementById('qr-image').offsetWidth
    });
    this.$socket.on('qr_url',url=>{
      if(typeof url != 'string') 
        return
      else
        this.qrUrl = url;
    })
    this.$socket.on('auth_fail',err=>{
      console.log({err})
    })
    this.$socket.on('is_auth',res=>{
      if(res)
        this.authRoute();
    })
    const auth = await genericPostRequests('/auth_control',{token:this.$token})
    if(auth.data.status)
      this.authRoute();
  },
  methods:{
    ...mapMutations([
      "setLogs",
      "setSelectedPath"
    ]),
    authRoute(){
      this.setLogs('Client is preparing...')
      this.$router.push(this.$route.query.redirect|| '/chat');
      let item = {
        title:"Whatsapp Accounts",
        api:'/chat'
      }
      this.setSelectedPath(item);
    },
  }
}
</script>

<style lang="scss">
@import '@/scss/mixins.scss';
@import '@/scss/colors.scss';
.qr-main{
  height: 100%;
  @include d-flex-center;
  width: 100%;
  .qr-card{
    @include d-flex(row,flex-start,center);
    border-radius: 20px;
    width: 90%;
    height: 90%;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 28px -6px rgba(0,0,0,0.39); 
    box-shadow: 0px 0px 28px -6px rgba(0,0,0,0.39);
    .qr{
      width: 100%;
      @include d-flex(row,space-between,center);
      .qr-text{
        width: 100%;
        height: 100%;
        @include d-flex(column,flex-start,flex-start);
        padding: 1vw;
        .qr-text-header{
          font-size: 1.25vw;
          font-weight: 700;
          margin-bottom: 1.25vw;
        }
        .to-do-list{
          padding: 0.75vw 0px 0.75vw 0px;
          font-size: 1.15vw;
          font-weight: normal;
        }
      }
      .qr-image{
        width:50vw;
        @include d-flex-center;
        .qr-img{
          width: 70%;
        }
        .qr-loading{
          @include d-flex-center;
          background-color: #fff;
          width: 70%;
          border-radius: 1vw;
          -webkit-box-shadow: 0px 0px 7px 0px #000000; 
          box-shadow: 0px 0px 7px 0px #000000;
        }
      }
    }
  }
}
</style>