<template>
<div class="navbar-main">
    <img @click="routeDashboard" class="navbar-logo" src="@/assets/images/whapichat_logo.svg" alt="">
    <div class="user-and-lang">
        <div @click="loginForm" class="user">
            <img class="user-icon" src="@/assets/icons/user.svg" alt="">
        </div>
        <div class="lang">
            <div class="flag">
                <img class="flag-icon" src="@/assets/icons/turkey_flag.svg" alt="">
            </div>
            <div class="flag-name">
                TR
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {mapMutations,mapGetters} from "vuex"
export default {
    data(){
        return{
            showLoginForm:false,
        }
    },
    methods:{
        routeDashboard(){
            this.$router.push(this.$route.query.redirect|| '/chat');
            let item = {
                title:"Dashboard",
                api:'/'
            }
            this.setSelectedPath(item);
        },
        ...mapMutations([
            "setShowUserLoginForm"
        ]),
        loginForm(){
            this.showLoginForm = !this.showLoginForm;
            this.setShowUserLoginForm(this.showLoginForm);
        }
    },
}
</script>

<style lang="scss">
@import '@/scss/colors.scss';
@import '@/scss/mixins.scss';

.navbar-main{
    width: 100%;
    background-color: #ececec;
    padding: 0.8vw;
    @include d-flex(row,space-between,center);
    -webkit-box-shadow: 0px 2px 17px -2px rgba(0,0,0,0.41); 
    box-shadow: 0px 2px 17px -2px rgba(0,0,0,0.41);
    .navbar-logo{
        cursor:pointer;
        width:7vw;
        user-select:none;
    }
    .user-and-lang{
        @include d-flex(row,space-around,center);
        position: relative;
        .user{
            @include d-flex-center;
            padding: 0.3vw;
            transition: background-color .1s ease-in;
            border-radius: 999px;
            cursor: pointer;
            &:hover{
                background-color: #b9b9b9;
            }
            .user-icon{
                width: 0.95vw;
            }
        }
        .lang{
            @include d-flex(row,center,stretch);
            margin: 0 0.75vw 0 0.75vw;
            cursor: pointer;
            transition: background-color .1s ease-in;
            border-radius: 4px;
            &:hover{
                background-color: #b9b9b9;
            }
            .flag{
                @include d-flex-center;
                .flag-icon{
                    width: 0.9vw;
                    margin-top: 2px;
                }
            }
            .flag-name{
                @include d-flex-center;
                padding: 0.1vw;
                font-size: 0.8vw;
                font-weight: 700;
            }
        }
        
    }
}
</style>