<template>
  <div id="terminal_main" class="terminal-main">
      <transition name="slide" mode="out-in">
          <div key="logs" v-if="!isMinimizeLogsCard" class="logs">
              <div class="main-header">
                  <div class="card-header">
                      <div class="header-icons">
                          <img class="icons" src="@/assets/icons/logs.svg" alt="">
                      </div>
                      <div class="header-title">
                        Logs
                      </div>
                  </div>
                  <div class="right-buttons">
                      <div @click="clearLogs" class="trash-icon">
                          <img class="mini-icon" src="@/assets/icons/delete.svg" alt="">
                      </div>
                      <div @click="minimizeFunc('log')" class="minimize-icon">
                          <img class="mini-icon" src="@/assets/icons/minimize.svg" alt="">
                      </div>
                  </div>
              </div>
              <div class="card-content">
                  <template v-if="getLogs.length!=0">
                      <div v-for="(item,i) in getLogs" :key="'logs'+i" style="margin-bottom:5px;word-wrap: break-word;">
                          <div>
                               {{item}}
                          </div>
                      </div>
                  </template>
                  <template v-else>
                    <div>
                        Waiting for events
                    </div>
                </template>
              </div>
          </div>
          <div key="minimize_logs" v-else class="minimize-logs">
              <div @click="minimizeFunc('log')" class="minimize-icon">
                  <img class="mini-icon" src="@/assets/icons/reverse-minimize.svg" alt="">
              </div>
              <div class="rotated-logs-text">
                  Logs
              </div> 
              <div class="header-icons">
                  <img class="icons" src="@/assets/icons/logs.svg" alt="">
              </div>
          </div>
      </transition>
      <transition name="slide" mode="out-in">
          <div key="dev_terminal" v-if="!isMinimizeTerminalCard" class="logs">
              <div class="main-header">
                  <div class="card-header">
                      <div class="header-icons">
                          <img class="icons" src="@/assets/icons/dev_terminal.svg" alt="">
                      </div>
                      <div class="header-title">
                        Dev Terminal
                      </div>
                  </div>
                  <div class="right-buttons">
                      <div @click="clearDevTerminal" class="trash-icon">
                          <img class="mini-icon" src="@/assets/icons/delete.svg" alt="">
                      </div>
                      <div @click="minimizeFunc('terminal')" class="minimize-icon">
                          <img class="mini-icon" src="@/assets/icons/minimize.svg" alt="">
                      </div>
                  </div>
              </div>
              <div class="card-content">
                  <template v-if="getDevTerminal!=''">
                      {{getDevTerminal}}
                  </template>
                  <template v-else>
                    <div>
                        Waiting for events
                    </div>
                </template>
              </div>
          </div>
          <div key="minimize_dev_terminal" v-else class="minimize-logs">
              <div @click="minimizeFunc('terminal')" class="minimize-icon">
                  <img class="mini-icon" src="@/assets/icons/reverse-minimize.svg" alt="">
              </div>
              <div class="rotated-logs-text">
                  Dev Terminal
              </div> 
              <div class="header-icons">
                  <img class="icons" src="@/assets/icons/dev_terminal.svg" alt="">
              </div>
          </div>
      </transition>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from "vuex"
export default { 
    data(){
        return{
            isMinimizeLogsCard:false,
            isMinimizeTerminalCard:false,
        }
    },
    methods:{
        ...mapMutations([
            "clearLogs",
            "clearDevTerminal"
        ]),
        minimizeFunc(item){
            if(item == 'log'){
                this.isMinimizeLogsCard = !this.isMinimizeLogsCard;
            }
            else{
                this.isMinimizeTerminalCard = !this.isMinimizeTerminalCard;
            }
        }
    },
    computed:{
        ...mapGetters([
            "getLogs",
            "getDevTerminal",
        ]),

    },
    watch:{
        // getLogs:{
        //     immediate:true,
        //     handler(){
        //         setTimeout(() => {
        //             let element = document.getElementById("logs");
        //             element.scrollTop = 999999;
        //         }, 50);
        //     }
        // },
    }
}
</script>

<style lang="scss">
@import '@/scss/colors.scss';
@import '@/scss/mixins.scss';

.terminal-main{
    height: 100%;
    width: 100%;
    @include d-flex(column,space-around,flex-end);
    word-wrap: break-word;
    .minimize-logs{
        position: relative;
        height: 23vw;
        border-radius: 10px 0px 0px 10px;
        background-color: #012140;
        @include d-flex(column,space-between,center);
        padding: 0.5vw 0.6vw 0.5vw 0.6vw;
        .minimize-icon{
            @include d-flex-center;
            padding: 0.15vw;
            border-radius: 99px;
            cursor: pointer;
            transition: all .1s ease-in;
            &:hover{
                background-color: #9E9E9E;
            }
        }
        .rotated-logs-text{
            @include d-flex-center;
            user-select: none;
            font-size: 1.1vw;
            color: #fff;
            writing-mode: vertical-rl;
            transform: rotate(180deg);
            flex: 1 0 1px;
        }
        .header-icons{
            padding: 0.15vw;
            @include d-flex-center;
            user-select: none;
            flex: 0 0 auto;
            .icons{
                width: 1.4vw;
            }
        }
    }
    .logs{
        width: 20vw;
        height: 48%;
        border-radius: 10px 0px 0px 10px;
        background-color: #012140;
        @include d-flex(column,flex-start,center);
        word-wrap: break-word;
        .main-header{
            color: #fff;
            font-size: 1.1vw;
            padding: 0.25vw;
            border-bottom: 1px solid #ADADAD;
            width: 100%;
            flex: 0 0 auto;
            @include d-flex(row,space-between,center);
            .card-header{
                @include d-flex(row,flex-start,center);
                .header-icons{
                    padding: 0.15vw;
                    @include d-flex-center;
                    user-select: none;
                    .icons{
                        width: 1.32vw;
                    }
                }
                .header-title{
                    @extend .header-icons;
                    font-size: 1vw;
                }
            }
            .right-buttons{
                @include d-flex(row,flex-start,center);
                .trash-icon{
                    @include d-flex-center;
                    padding: 0.15vw;
                    margin-right: 0.25vw;
                    border-radius: 99px;
                    cursor: pointer;
                    transition: all .1s ease-in;
                    &:hover{
                        background-color: #9E9E9E;
                    }
                    .mini-icon{
                        width: 1.15vw;
                    }
                }
                .minimize-icon{
                    @extend .trash-icon;
                    margin-right: 0;
                }
            }
        }
        .card-content{
            width: 100%;
            color: #fff;
            padding: 0.25vw;
            @include d-flex(column,flex-start,flex-start);
            overflow-y: auto;
            overflow-x: hidden;
            word-break: break-all; 
            word-wrap: break-word;
            overflow-wrap: break-word;
            white-space: pre-wrap;
            flex: 1 0 1px;
            font-size: 0.85vw;
            &::-webkit-scrollbar {
                width: 0.25vw;
            }

            &::-webkit-scrollbar-thumb {
                background: rgb(145, 145, 145); 
                border-radius: 10px;
            }
        }
    }
}

.slide-enter-active{
  animation: slide-in .25s ease-out forwards;
}
.slide-leave-active{
  animation: slide-out .25s ease-in forwards;
}
.slide-move{
    transition: transform 1s;
}

@keyframes slide-in {
  from{
    transform: translateX(100%);
  }
  to{
    transform: translateX(0px);
  }
}
@keyframes slide-out {
  from{
    transform: translateX(0px);
  }
  to{
    transform: translateX(100%);
  }
}
</style>