import Vue from 'vue'
import Vuex from 'vuex'
import _ from "lodash"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showUserLoginForm:false,
    logs:[],
    devTerminal:"",
    selectedPath:{
      title:null,
      api:null,
    },
  },
  mutations: {
    setSelectedPath(state,item){
      // state.selectedPath.title = item.title;
      state.selectedPath.api = item.api;
    },
    setLogs(state,item){
      state.logs.push(item)
    },
    setDevTerminal(state,item){
      state.devTerminal = item
    },
    setShowUserLoginForm(state,item){
      state.showUserLoginForm = item
    },
    clearLogs(state){
      state.logs = [];
    },
    clearDevTerminal(state){
      state.devTerminal = ""
    }
  },
  getters:{
    getSelectedPath(state){
      return state.selectedPath
    },
    getShowUserLoginForm(state){
      return state.showUserLoginForm
    },
    getLogs(state){
      return state.logs
    },
    getDevTerminal(state){
      return state.devTerminal
    }
  }
})
