<template>
    <div class="converter-view">

        <page-header></page-header>

        <div class="base64converter">
            <div class="file-input-area">
                <div v-if="!base64Control" class="title">
                    Add your files here:
                </div>
                <div v-else class="title">
                    Your file:
                </div>
                <template v-if="files.length > 0">
                    <div class="file-info">
                        <div class="file-info-content" v-for="file in files" :key="file.name">
                            <div>File Name: {{file.name}}</div>
                            <div>File Size: {{file.size}} Bytes</div>
                            <div>File Type: {{file.type}}</div>
                            <div class="change-file-button" @click="removeFiles">Change File</div>
                        </div>
                    </div>
                </template>
                <template v-else>
                    <div @mousedown="dragOver" @mouseup="dragLeave" @mouseleave="dragLeave" @dragover="dragOver"
                        @mouseenter="dragOver" @dragleave="dragLeave" @drop="dragLeave" class="upload-file"
                        :style="dragEnter ? 'background-color: #eeeeee; color:#305691;' : 'background-color:#ebebeb; color:#000;'">
                        <input @change="handleFileInput" type="file">
                        <div style="font-size:1.6vmin;">Click or drag to insert</div>
                    </div>
                </template>
            </div>
            <v-slide-x-transition>
                <div v-if="base64Control" class="base64-content-area">
                    <div class="title">
                        Base64:
                    </div>
                    <div class="base64String">
                        {{base64String}}
                    </div>
                </div>
            </v-slide-x-transition>
        </div>
        <div class="info-content">
        </div>
    </div>
</template>

<script>
    import PageHeader from "../components/PageHeader.vue"
    export default {
        data() {
            return {
                files: [],
                cardColor: "#ebebeb",
                cardTextColor: "#000",
                dragEnter: false,
                hover: false,
                base64Control: false,
                base64String: ""
            }
        },
        components:{
            PageHeader
        },
        methods: {
            // handleFileDrop(e) {
            //     let droppedFile = e.dataTransfer.files[0];
            //     if (!droppedFile) return;
            //     this.files.push(droppedFile);
            //     this.createBase64(droppedFile);
            // },
            handleFileInput(e) {
                let file = e.target.files[0];
                if (!file) return;
                this.files.push(file);
                this.createBase64(file);
            },
            createBase64(fileObject) {
                const reader = new FileReader();
                reader.onload = (e) => {
                    this.base64Control = true;
                    this.base64String = e.target.result;
                }
                reader.readAsDataURL(fileObject);
            },
            dragOver() {
                this.dragEnter = true
            },
            dragLeave() {
                this.dragEnter = false
            },
            removeFiles() {
                this.files = []
                this.base64Control = false
                this.base64String = ""
            }
        }
    }
</script>

<style lang="scss">
    @import "@/scss/mixins.scss";

    .converter-view {
        @include d-flex(column,flex-start,stretch);
        width: 100%;
        height: 100%;
        flex: 1 0 1px;
        font-family: var(--whapichat-font);
        overflow: hidden;


        .base64converter {
            @include d-flex(row,flex-start,stretch);
            width: 100%;
            max-height: 40%;
            flex: 1 0 1px;

            .file-input-area {
                @include d-flex(column,flex-start,stretch);
                gap: 2%;
                padding: .5% 1%;
                flex: 1 0 50%;

                .title {
                    flex: 0 0 auto;
                    font-size: 1.6vmin;
                }

                .file-info {
                    @include d-flex(column,center,center);
                    border-radius: 3px;
                    background-color: #ebebeb;
                    flex: 1 0 1px;
                    @include box-shadow;

                    .file-info-content {
                        @include d-flex(column,flex-start,center);
                        gap: 20px;
                        font-size: 1.4vmin;

                        .change-file-button {
                            @include d-flex(row,flex-start,center);
                            cursor: pointer;
                            background-color: var(--whapichat-blue);
                            ;
                            color: white;
                            padding: 3% 6%;
                            border-radius: 20px;
                            font-size: 1.2vmin;
                        }
                    }
                }

                .upload-file {
                    position: relative;
                    @include d-flex(row,center,center);
                    border-radius: 3px;
                    flex: 1 0 100%;
                    @include box-shadow;

                    input {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        cursor: pointer;
                        opacity: 0.0;
                        filter: alpha(opacity=0);
                    }

                    &:hover {
                        color: var(--whapichat-blue);
                    }
                }
            }

            .base64-content-area {
                @include d-flex(column,flex-start,stretch);
                gap: 2%;
                flex: 1 0 100%;
                padding: .5%;

                .title {
                    flex: 0 0 auto;
                    font-size: 1.6vmin;
                }

                .base64String {
                    flex: 1 0 1px;
                    max-width: 49%;
                    padding: 1vmin;
                    font-size: 1.4vmin;
                    word-break: break-all;
                    overflow: auto;
                    scroll-behavior: smooth;
                    border-radius: 3px;
                    background-color: #ebebeb;
                    @include box-shadow;
                    

                    &::-webkit-scrollbar {
                        width: .5vmin;
                        height: .5vmin;
                        border-radius: 10px;
                    }

                    &::-webkit-scrollbar-thumb {
                        background: #aca9a9;
                        border-radius: 10px;
                    }
                }
            }
        }
    }

    @media screen and (max-width:1024px) {
        .converter-view {
            .base64converter {
                @include d-flex(column,flex-start,stretch);
                max-height: 35%;

                .base64-content-area{
                    .base64String{
                        max-width: 100%;
                    }
                }
            }
        }
    }
    @media screen and (max-width:768px) {
        .converter-view {
            .base64converter {
                @include d-flex(column,flex-start,stretch);
                max-height: 28%;
            }
        }
    }
    @media screen and (max-width:425px) {
        .converter-view {
            .base64converter {
                @include d-flex(column,flex-start,stretch);
                max-height: 15%;
            }
        }
    }
    @media screen and (max-width:375px) {
        .converter-view {
            .base64converter {
                @include d-flex(column,flex-start,stretch);
                max-height: 10%;
            }
        }
    }
</style>