<template>
  <div class="chat-main">
    <div v-if="!loadingChat" class="chat-main-card">
      <div class="get-info">
        <div style="display:flex;align-items:center;justify-content:flex-start">
          <div @click="contactMenu" v-if="smallScreen" class="contact-menu-icon">
            <img v-if="isSmalled" src="@/assets/icons/open_contact.svg" alt="">
            <img v-else src="@/assets/icons/close_contact.svg" alt="">
          </div>
          <div @click="terminalFunction('get_info')" @mouseenter="openPopup('get','get_info',null)"
          @mouseleave="closePopup" class="account-name">
            <div class="wp-icon-div">
              <img class="wp-icon" src="@/assets/icons/whatsapp.svg" alt="">
            </div>
            <div class="account-name-text">
              {{userNumber}}
            </div>
          </div>
        </div>
        <div class="account-name-token">
          {{this.$token}}
        </div>
      </div>
      <div class="chat-screen">
        <transition tag="div" class="img-slider" name="slide">
          <div v-if="!isSmalled" :style="smallScreen ?'position:absolute;z-index:99999' :''" id="bigContact" class="contact-side">
            <div class="contact-header">
              <div @click="terminalFunction('get_profile_picture')"
                @mouseenter="openPopup('get','get_profile_picture',null)" @mouseleave="closePopup" class="pp-image">
                <img class="pp" :src="profileImage" alt="">
              </div>
              <div class="dot-extra" style="position:relative">
                <div @click.stop="showCard" class="dot-icon">
                  <img class="dot" src="@/assets/icons/three_dot.svg" alt="">
                </div>
                <div id="extra_card" v-if="showExtraCard" class="dot-icon-card">
                  <div class="extras" v-for="(item,i) in extras" @click="extrasFunc(item)" :key="'extra'+i">
                    {{item.title}}
                  </div>
                </div>
              </div>
            </div>
            <div class="search">
              <div class="search-input-div">
                <div class="search-input-icon">
                  <img class="search-icon" src="@/assets/icons/search.svg" alt="">
                </div>
                <input @keyup="searchContact" v-model="searchInput" placeholder="Search Chat" class="search-input"
                  type="text">
              </div>
              <div @click="reloadChat" @mouseenter="openPopup('get','get_contacts',null)" @mouseleave="closePopup"
                class="reload-button">
                <img class="reload" src="@/assets/icons/reload.svg" alt="">
              </div>
            </div>
            <div class="contacts">
              <div @mouseenter="openPopup('get','get_message_history',item)" @mouseleave="closePopup"
                @click="getMessageHistory(item)" v-for="(item,i) in historyContact" :key="'chat'+i"
                :class="item.id._serialized == selectedChat ? 'selected-contacts-content' :'contacts-content'">
                <div class="contacts-info">
                  <div class="pp-image">
                    <img class="pp" src="@/assets/icons/contact_user.svg" alt="">
                  </div>
                  <div class="name">
                    {{item.name}}
                  </div>
                </div>
                <div class="contact-time">
                  <div class="time">
                    {{timeStampToHour(item)}}
                  </div>
                  <div v-if="item.unreadCount!=0" class="unread">
                    {{item.unreadCount}}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
        <div v-if="selectedChat!=''" class="chat-side">
          <div :style="smallScreen ? 'border-left:none':''" class="selected-user">
            <div class="selected-user-image">
              <img class="selected-user-pp" src="@/assets/icons/contact_user.svg" alt="">
            </div>
            <div class="selected-user-text">
              {{selectedChatName}}
            </div>
          </div>
          <div v-if="historyMessages.length!=0" id="messages" class="messages">
            <div v-for="(item,i) in historyMessages" :key="'messages'+i"
              :class="item.fromMe ? 'all-messages-right' :'all-messages-left'">
              <div :style="item.fromMe ? 'background-color:#D7ECFE':'background-color:#fff'" class="message-content">
                <div v-if="selectedChat.split('@')[1] == 'g.us'" class="message-name">
                  {{item.name}}
                </div>
                <div :style="selectedChat.split('@')[1] == 'g.us' ? 'padding:0.2vw 0.5vw 0.5vw 0.5vw':'padding:0.5vw'"
                  class="message">
                  <div v-if="item.links.length == 0 && !item.hasMedia && item.type =='chat'" class="text">
                    {{item.body}}
                  </div>
                  <div v-else-if="item.links.length != 0 && item.type == 'chat'" class="text">
                    <span v-for="(content,k) in item.body" :key="'content'+k" style="padding-right:0.2vw">
                      <a v-if="content.type=='link'" :href="content.src" target="_blank">{{content.body}}</a>
                      <span v-else>{{content.body}}</span>
                    </span>
                  </div>
                  <div v-else-if="item.hasMedia" class="images">
                    <div v-if="item.loading && (item.type === 'image'||item.type==='video')" class="loading-image">
                      <v-progress-circular :size="30" :width="5" color="#8a8a8a" indeterminate></v-progress-circular>
                    </div>
                    <div v-else-if="item.loading && item.type === 'document'" class="loading-file">
                      <v-progress-circular :size="30" :width="5" color="#8a8a8a" indeterminate></v-progress-circular>
                    </div>
                    <div v-else-if="item.body != '' || item.type ==='document'" class="media">
                      <!-- <img src="https://cdn.glitch.com/0e4d1ff3-5897-47c5-9711-d026c01539b8%2Fbddfd6e4434f42662b009295c9bab86e.gif?v=1573157191712" alt="this slowpoke moves"  width="250"> -->
                      <video style="width:12vw;border-radius:4px" controls v-if="item.type == 'video'">
                        <source type="video/mp4" :src="item.mediaKey">
                      </video>
                      <img v-else-if="item.type!='document'" style="width:12vw;border-radius:4px" :src="item.mediaKey" alt="">
                      <template v-if="item.type!='document'">
                        <div class="media-text">
                            <div style="max-width:12vw" class="text">
                              {{item.body}}
                            </div>
                            <div class="text-time">
                              {{timeStampToHour(item)}}
                            </div>
                        </div>
                      </template>
                      <template v-else>
                        <a :download="item.body" :href="item.mediaKey" style="display:flex;flex-direction:row;align-items:flex-end;cursor:pointer;color:#000">
                          <div class="download-file">
                          <div class="document-icon">
                            <img style="width:1vw" v-if="item.mediaKey.split(';')[0].split(':')[1] === 'application/pdf'" src="@/assets/icons/pdf.svg" alt="">
                            <img style="width:1vw" v-else-if="item.mediaKey.split(';')[0].split(':')[1] === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'" src="@/assets/icons/excel.svg" alt="">
                            <img style="width:1vw" v-else-if="item.mediaKey.split(';')[0].split(':')[1] === 'text/plain'" src="@/assets/icons/txt.svg" alt="">
                            <img style="width:1vw" v-else-if="item.mediaKey.split(';')[0].split(':')[1] === 'application/x-rar-compressed'" src="@/assets/icons/rar.svg" alt="">
                          </div>
                          <div style="max-width:12vw" class="text">
                              {{stringShorter(item.body,28)}}
                            </div>
                        </div>
                        <div class="text-time">
                              {{timeStampToHour(item)}}
                            </div>
                        </a>
                      </template>

                    </div>
                    <div class="none-text-image" v-else>
                      <!-- <img src="https://cdn.glitch.com/0e4d1ff3-5897-47c5-9711-d026c01539b8%2Fbddfd6e4434f42662b009295c9bab86e.gif?v=1573157191712" alt="this slowpoke moves"  width="250"> -->
                      <video style="width:12vw;border-radius:4px" controls v-if="item.type == 'video'">
                        <source type="video/mp4" :src="item.mediaKey">
                      </video>
                      <img v-else style="width:12vw;border-radius:4px" :src="item.mediaKey" alt="">
                      <div class="text-time-image">
                        {{timeStampToHour(item)}}
                      </div>
                    </div>
                  </div>
                  <div v-else>
                    /Different Type/
                  </div>
                  <div v-if="!item.hasMedia" class="text-time">
                    {{timeStampToHour(item)}}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-else class="loading-messages">
            <v-progress-circular :size="loadingSize" :width="loadingSize/10" color="#8a8a8a" indeterminate></v-progress-circular>
          </div>
          <div class="send-messages">
            <div class="buttons">
              <div style="position:relative">
                <div @click.stop="isOpenPaperclipCard = !isOpenPaperclipCard" class="paperclip">
                  <img style="width:1.1vw" src="@/assets/icons/paperclip.svg" alt="">
                </div>
                <div id="paperclip-card" v-if="isOpenPaperclipCard" class="paperclip-card">
                  <div @mouseenter="openPopup('post',null,item)" @mouseleave="closePopup" @click.stop="uploadImage"
                    v-for="(item,i) in paperclipItems" :key="'item'+i" class="paperclip-card-item">
                    <div class="item-image">
                      <img class="image-icon" :src="require('@/assets/icons/'+item.src)" alt="">
                    </div>
                    <div class="item-text">
                      {{item.title}}
                    </div>
                  </div>
                  <input @change="onChange" id="selectedFile" style="display:none" type="file">
                </div>
              </div>
              <emoji-picker style="position:relative">
                <div slot="emoji-invoker" slot-scope="{ events: { click: clickEvent } }" @click.stop="clickEvent">
                  <button class="emoji-button" type="button"><img style="width:1.1vw" src="@/assets/icons/emoji.svg"
                      alt=""></button>
                </div>
                <div class="emoji-group" slot="emoji-picker" slot-scope="{emojis}">
                  <div class="emoji-group-header">
                    <div @click="selectedEmojiGroup(category)"
                      :class="emojiGroupName == category ? 'selected-emoji-headers' :'emoji-headers'"
                      v-for="(category,i) in emojiGroups" :key="'emojiGroup'+i">
                      {{category}}
                    </div>
                  </div>
                  <div class="emoji-content">
                    <div class="emoji-content-group" v-for="(emojiGroup, category) in emojis" :key="category">
                      <template v-if="category == emojiGroupName">
                        <div @click="addEmoji(emoji)" v-for="(emoji, emojiName) in emojiGroup" :key="emojiName"
                          class="emojis">
                          {{emoji}}
                        </div>
                      </template>
                    </div>
                  </div>
                </div>
              </emoji-picker>
            </div>
            <div class="message-input">
              <input placeholder="Message" v-model="message" spellcheck="false" type="text" class="message-text-input">
            </div>
            <div class="send-button">
              <div @mouseenter="openPopup('post','send_message',null)" @mouseleave="closePopup" @click="sendMessage"
                class="send">
                <img style="width:1.1vw" src="@/assets/icons/send.svg" alt="">
              </div>
            </div>
          </div>
        </div>
        <div v-else class="not-selected-chat">
          <img style="width:13vw;filter: grayscale(90%);user-select:none" src="@/assets/images/whapichat_logo.svg"
            alt="">
        </div>
      </div>
    </div>
    <div v-else class="loading-screen">
      <div class="loading-item">
        <div class="loading-text">
          Loading
        </div>
        <div class="dots-bars-6"></div>
      </div>
    </div>
    <app-send-message-modal v-if="openModal" :isOpen.sync="openModal" :image="b64Object"
      :messageHistory.sync="historyMessages" :selectedChat.sync="selectedChat" :type="modalType">
    </app-send-message-modal>
    <!-- <app-follower-popup id="popup" v-if="isOpenPopup" class="popup" :data="popupObj"></app-follower-popup> -->
  </div>
</template>

<script>
  import {
    genericGetRequests,
    genericPostRequests
  } from "@/api/axios.js"
  import EmojiPicker from 'vue-emoji-picker'
  import moment from "moment"
  import _ from "lodash"
  import sendMessageModal from "@/components/chat_page_components/sendMessageModal.vue"
  import followerPopup from "@/components/chat_page_components/followerPopup.vue"
  import Vue from 'vue';
  import {
    mapMutations
  } from "vuex"
  export default {
    data() {
      return {
        modalType: "",
        showExtraCard: false,
        searchInput: "",
        isOpenPopup: false,
        openModal: false,
        userNumber: "",
        profileImage: "",
        historyContact: [],
        selectedChat: "",
        selectedChatName: null,
        historyMessages: [],
        message: "",
        search: '',
        emojiGroupName: "People",
        emojiGroups: [
          "People",
          "Nature",
          "Objects",
          "Places",
          "Symbols",
        ],
        paperclipItems: [{
          title: 'File',
          src: 'file.svg',
          value: 'file'
        }],
        isOpenPaperclipCard: false,
        b64Object: {},
        loadingChat: true,
        popupObj: {},
        mainUrl: 'http:localhost:8085',
        tempHistoryContact: [],
        extras: [{
            title: "New Contact",
            value: "new_contact"
          },
        ],
        isSmalled:false,
        isAbsolute:false,
        smallScreen:false,
        loadingSize:50,
      }
    },
    components: {
      EmojiPicker,
      appSendMessageModal: sendMessageModal,
      appFollowerPopup: followerPopup,
    },
    methods: {
      stringShorter(str, len = 16) {
          if(str.length > len)
            return str.substring(0, len) + '...'
          else
            return str
      },
      contactMenu(){
        this.isSmalled = !this.isSmalled
      },
      extrasFunc(item) {
        if (item.value == 'new_contact') {
          this.modalType = item.value
          this.openModal = true;
        }
      },
      showCard() {
        this.showExtraCard = !this.showExtraCard;
      },
      searchContact(e) {
        if ((e.keyCode >= 65 && e.keyCode <= 90) || e.keyCode == 46 || e.keyCode == 8 || (e.keyCode >= 96 && e
            .keyCode <= 105) || (e.keyCode >= 219 && e.keyCode <= 221) || e.keyCode == 191 || e.keyCode == 186)
          this.historyContact = _.cloneDeep(this.tempHistoryContact.filter(res => _.upperCase(res.name).includes(_
            .upperCase(this.searchInput))))

        if (this.searchInput == "")
          this.historyContact = _.cloneDeep(this.tempHistoryContact)
      },
      async terminalFunction(url) {
        let response = null
        if (url == 'get_info') {
          response = await genericGetRequests(`/get_info/${this.$token}`)
          this.setLogs(response)
          this.setDevTerminal(
            `curl -H 'Content-Type:application/json' \ -H 'Authorization':'${this.$token}' ${this.mainUrl}/get_info`
            )
        } else if (url == 'get_profile_picture') {
          response = await genericGetRequests(`/get_profile_picture/${this.$token}`)
          this.setLogs(response)
          this.setDevTerminal(
            `curl -H 'Content-Type:application/json' \ -H 'Authorization':'${this.$token}' ${this.mainUrl}/get_profile_picture`
            )
        }

      },
      async reloadChat() {
        const response = await genericGetRequests(`/get_contacts/${this.$token}`)
        this.historyContact = _.cloneDeep(response.response);
        this.tempHistoryContact = _.cloneDeep(response.response);
        this.selectedChat = ""
        this.setLogs(this.historyContact)
        this.setDevTerminal(
          `curl -H 'Content-Type:application/json' \ -H 'Authorization':'${this.$token}' ${this.mainUrl}/get_contacts`)
      },
      listerFunc(e) {
        let popup = document.getElementById('popup');
        popup.style.left = (e.pageX/window.innerWidth)*100 + 1 +   'vw';
        popup.style.top = (e.pageY/window.innerWidth)*100 + (-7)+ 'vw';
      },
      openPopup(type, api, item) {
        // if (api == 'get_message_history') {
        //   this.popupObj = {
        //     type,
        //     number: item.id.user,
        //     limit: 50,
        //     api,
        //   }
        // } else if (api == 'get_contacts' || api == 'get_profile_picture' || api == 'get_info') {
        //   this.popupObj = {
        //     type,
        //     number: null,
        //     limit: null,
        //     api,
        //   }
        // } else if (item && item.hasOwnProperty('value') && item.value == 'image') {
        //   this.popupObj = {
        //     type,
        //     number: null,
        //     limit: null,
        //     api: 'send_file',
        //     body: {
        //       number: '<String>',
        //       b64: '<String>',
        //       mimeType: '<String>',
        //       imageName: '<String>'
        //     }
        //   }
        // } else if (api == 'send_message') {
        //   this.popupObj = {
        //     type,
        //     number: null,
        //     limit: null,
        //     api,
        //     body: {
        //       number: '<String>',
        //       message: '<String>',
        //     }
        //   }
        // }
        // this.isOpenPopup = true;
        // document.addEventListener('mousemove', this.listerFunc, false);
      },
      closePopup() {
        this.isOpenPopup = false;
        document.removeEventListener('mousemove', this.listerFunc)
      },
      onChange(event) {
        // Resim gnderme
        if (event.target.files && event.target.files[0]) {
          const name = event.target.files[0].name;
          var data = this
          let fileType;
          const reader = new FileReader();
          if(event.target.files[0].type == 'image/png'||event.target.files[0].type == 'image/jpg'||event.target.files[0].type == 'image/jpeg'||event.target.files[0].type == 'image/gif'||event.target.files[0].type == 'image/svg'){
            fileType = 'image';
          }
          else if(event.target.files[0].type == 'video/mp4'){
            fileType = 'video'
          }
          else{
            fileType = 'document'
          }
          reader.addEventListener("load", async function (e) {
            this.b64 = e.target.result.split(',')[1]
            this.b64Type = e.target.result.split(',')[0].split(':')[1].split(';')[0]
            //console.log({i:this.b64})
            let obj = {
              b64: this.b64,
              type: this.b64Type,
              b64Name: name,
              number: data.selectedChat.split('@')[0],
            }
            data.modalType = fileType;
            data.openModal = true
            data.b64Object = obj;
            //document.getElementById('selectedFile').value = "";
          });
          reader.readAsDataURL(event.target.files[0]);
        }
      },
      async uploadImage() {
        this.isOpenPopup = false;
        document.removeEventListener('mousemove', this.listerFunc)
        document.getElementById('selectedFile').click();
        this.isOpenPaperclipCard = false;
      },
      addEmoji(emoji) {
        this.message += emoji;
      },
      selectedEmojiGroup(category) {
        this.emojiGroupName = category;
      },
      async sendMessage() {
        if (this.message != "" && this.selectedChat != "") {
          let response
          let chat = this.tempHistoryContact.find(res => res.id._serialized == this.selectedChat)
          if (chat.isGroup) {
            response = await genericPostRequests(`/send_message/${this.$token}`, {
              name: chat.name,
              message: this.message,
            })
          } else {
            response = await genericPostRequests(`/send_message/${this.$token}`, {
              number: this.selectedChat.split('@')[0],
              name: chat.name,
              message: this.message,
            })
          }

          if (response.data.status) {
            this.historyMessages.push(response.data.response);
            setTimeout(() => {
              let element = document.getElementById("messages");
              element.scrollTop = 999999;
            }, 150);
          } else {
            this.historyMessages.push({
              body: response.data.err_desc,
              fromMe: true,
              type: 'chat',
              hasMedia: false,
              links: [],
              timestamp: moment().unix()
            })
          }
          this.setLogs(response.data)
          this.setDevTerminal(
            `curl -H '{'number':'${this.selectedChat.split('@')[0]}','message':'${this.message}'} \ -H 'Content-Type: application/json' \ -H 'Authorization':${this.$token}' -X POST ${this.mainUrl}/send_message`
            )
        }
        this.message = ""
      },
      async getMessageHistory(item) {
        if(window.innerWidth <= 1192)
          this.isSmalled = !this.isSmalled
        this.historyMessages = [];
        this.selectedChat = item.id._serialized;
        this.selectedChatName = item.name;
        const response = await genericPostRequests(`/send_seen/${this.$token}`, {
          id: item.id._serialized
        })
        if (response.data.status == 200) {
          item.unreadCount = 0;
        }
        const res = await genericGetRequests(`/get_message_history/${this.$token}/${item.id.user}/50`)
        let tempMsg = _.cloneDeep(res.response)
        this.setLogs(tempMsg)
        this.setDevTerminal(
          `curl -H 'Content-Type:application/json' \ -H 'Authorization':'${this.$token}' ${this.mainUrl}/get_message_history/${this.$token}/${item.id.user}/50`
          )
        let count = 0;
        for (let item of tempMsg) {
          if (item.links.length != 0) {
            let letterArr = item.body.split(" ")
            for (let letter of letterArr) {
              if (letter.includes(".") && item.links[count].link.includes(`${letter}`)) {
                if (typeof item.body == 'string')
                  item.body = [];
                item.body.push({
                  type: 'link',
                  src: item.links[count].link,
                  body: letter
                })

                count++;
              } else {
                if (typeof item.body == 'string')
                  item.body = [];
                item.body.push({
                  type: 'string',
                  body: letter,
                })
              }
            }
          }
          count = 0;
        }
        this.historyMessages = _.cloneDeep(tempMsg)
        //console.log(this.historyMessages)
        setTimeout(() => {
          let element = document.getElementById("messages");
          element.scrollTop = 999999;
        }, 150);
        let arr = _.cloneDeep(this.historyMessages);
        arr.reverse();
        let counter = this.historyMessages.length - 1;
        let i = 0;
        for await (let msg of arr) {
          if (msg.hasMedia) {
            let obj = {
              selectedNumber: this.selectedChat,
              token: this.$token,
              msg,
            }
            const response = await genericPostRequests('/load_file', obj)
            //console.log({response})
            if (response.data.response.status) {
              this.historyMessages[counter].mediaKey = response.data.response.key;
              this.historyMessages[counter].loading = false;
            }
          }
          counter--
        }
        //console.log({i:this.historyMessages});
      },
      timeStampToHour(item) {
        return moment.unix(item.timestamp).format('HH:mm')
      },
      async gettingMessage(item) {
        const response = await genericPostRequests(`/send_seen/${this.$token}`, {
          id: this.selectedChat
        })
        if (response.data.status == 200) {
          item.unreadCount = 0;
        }
        let count = 0;
        if (item.links.length != 0) {
          let letterArr = item.body.split(" ")
          for (let letter of letterArr) {
            if (letter.includes(".")) {
              if (typeof item.body == 'string')
                item.body = [];

              item.body.push({
                type: 'link',
                src: item.links[count].link,
                body: letter
              })
              count++;
            } else {
              if (typeof item.body == 'string')
                item.body = [];
              item.body.push({
                type: 'string',
                body: letter,
              })
            }
          }
        }
        let name;
        if (this.historyContact.find(contact => contact.id._serialized == item.author))
          name = this.historyContact.find(contact => contact.id._serialized == item.author).name
        else {
          if (item.author)
            name = '+' + item.author.split('@')[0]
          else
            name = ""
        }
        item.name = name
        this.historyMessages.push(item)
        setTimeout(() => {
          let element = document.getElementById("messages");
          element.scrollTop = 999999;
        }, 150);
      },
      ...mapMutations([
        "setLogs",
        "setDevTerminal"
      ])
    },
    async mounted() {
      genericGetRequests(`/is_sign_in/${this.$token}`).then(res=>{
        this.loadingChat = res.loading
        this.userNumber = res.userNumber.number
        this.profileImage = res.ppImg.data
        this.historyContact = res.chat
        this.tempHistoryContact = res.chat
      })
      window.onclick = event => {
        if (event.target != document.getElementById("extra_card") && this.showExtraCard == true)
          this.showExtraCard = false;


        if (event.target != document.getElementById("paperclip-card") && this.isOpenPaperclipCard == true)
          this.isOpenPaperclipCard = false;

      }
      window.addEventListener('resize',e=>{
        this.loadingSize = window.innerWidth/25;
        if(window.innerWidth <= 1192){
          this.smallScreen = true;
          Vue.nextTick(()=>{
            this.isSmalled = true;
          })
          
        }
        else{
          this.isSmalled = false;
          Vue.nextTick(()=>{
            this.smallScreen = false;
          })
        }
          
        
      })
      this.$socket.on('start_loading', data => {
        this.loadingChat = data;
      })
      this.$socket.on('user_number', res => {
        this.userNumber = res.number;
      })
      this.$socket.on('get_profile_image', res => {
        this.profileImage = res.data
      })
      this.$socket.on('get_contacts', res => {
        if (this.selectedChat != "") {
          res.data.forEach(item => {
            if (item.id._serialized == this.selectedChat) {
              item.unreadCount = 0;
            }
          });
        }
        this.historyContact = res.data
        this.tempHistoryContact = res.data
      })
      this.$socket.on('getting_message', item => {
        if (item.from == this.selectedChat) {
          //console.log({item});
          this.gettingMessage(item)
        }
      })
    }
  }
</script>

