            content: "";
            position: absolute;
            inset: 0 0 auto auto;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: currentColor;
            animation: db6-3 1.5s linear infinite;
          }

          @keyframes db6-0 {

            0%,
            30% {
              background-position: 0 0, 50% 0
            }

            33% {
              background-position: 0 100%, 50% 0
            }

            41%,
            63% {
