<template>
<transition name="slide" mode="out-in">
  <div v-if="getShowUserLoginForm" class="login">
      <div class="user-login-logo">
          <img class="whapichat" src="@/assets/images/whapichat_logo.svg" alt="">
      </div>
      <div class="login-inputs-main">
            <div key="header" class="font-apple inputs-header">
                {{inputsHeader}}
            </div>
            <transition-group name="login-slide" mode="out-in">
                
                <div key="sign-in" v-if="isSignIn">
                    <div class="login-inputs">
                        <div class="font-apple inputs" v-for="(item,i) in signInInputs" :key="'inputs'+i">
                            <input spellcheck="false" autocomplete="off" :name="item.title" class="font-apple input" :type="item.type" v-model="item.keyHolder" required>
                            <label class="font-apple input-label" :for="item.title">{{item.placeHolder}}</label>
                        </div>
                        <div class="forgot-password">
                            <span class="font-apple forgot-password-title">Forgot Password?</span>
                        </div>
                    </div>
                    <div class="sign-in">
                        <button class="font-apple sign-in-button">LOG IN</button>
                        <div class="font-apple or">
                            OR
                        </div>
                        <button class="font-apple google-button">
                            <img src="@/assets/icons/google.svg" alt="">
                            <div>Log in with Goggle</div>
                        </button>
                    </div>
                    <div class="font-apple not-account">
                        <div>
                            Don't have an account?
                        </div>
                        <div @click="changeInputs" class="new-account-link">
                            Create an account
                        </div>
                    </div>
                </div>
                <div key="log-out" v-else>
                    <div class="login-inputs">
                        <div class="font-apple inputs" v-for="(item,i) in signUpInputs" :key="'inputs'+i">
                            <input spellcheck="false" autocomplete="off" :name="item.title" class="font-apple input" :type="item.type" v-model="item.keyHolder" required>
                            <label class="font-apple input-label" :for="item.title">{{item.placeHolder}}</label>
                        </div>
                    </div>
                    <div style="padding:0" class="sign-in">
                        <button style="width:100%" class="font-apple sign-in-button">CREATE MY ACCOUNT</button>
                    </div>
                    <div class="font-apple not-account">
                        <div>
                            Already have an account?
                        </div>
                        <div @click="changeInputs" class="new-account-link">
                            Log in
                        </div>
                    </div>
                </div>
            </transition-group>
      </div>
  </div>
</transition>
</template>

<script>
import {mapGetters} from "vuex"
export default {
    data(){
        return{
            inputsHeader:"Log in to your account",
            isSignIn:true,
            signInInputs:[
                {title:'Email',type:'text',keyHolder:'',placeHolder:'Email*'},
                {title:'Password',type:'password',keyHolder:'',placeHolder:'Password*'},
            ],
            signUpInputs:[
                {title:'Email',type:'text',keyHolder:'',placeHolder:'Email*'},
                {title:'Password',type:'password',keyHolder:'',placeHolder:'Password*'},
                {title:'Password Confirm',type:'password',keyHolder:'',placeHolder:'Password(Confirm)*'},
            ]
        }
    },
    methods:{
        changeInputs(){
            this.isSignIn=!this.isSignIn;
            if(!this.isSignIn)
                this.inputsHeader = "Create an account"
            else
                this.inputsHeader = "Log in to your account"
        }
    },
    computed:{
        ...mapGetters([
        "getShowUserLoginForm"
        ])
    }
}
</script>

<style scoped>
.slide-enter-active{
  animation: slide-in .4s ease-out forwards;
}
.slide-leave-active{
  animation: slide-out .4s ease-in forwards;
}

.login-slide-enter-active{
  animation: login-slide-in .4s ease-out forwards;
}
.login-slide-leave-active{
  animation: login-slide-out .4s ease-in forwards;
  width: 100%;
}

@keyframes login-slide-in {
  from{
    transform: translateX(1000px);
  }
  to{
    transform: translateX(0px);
  }
}
@keyframes login-slide-out {
  from{
    transform: translateX(0px);
    position: absolute;
  }
  to{
    transform: translateX(1000px);
    position: absolute;
  }
}

@keyframes slide-in {
  from{
    transform: translateX(1000px);
  }
  to{
    transform: translateX(0px);
  }
}
@keyframes slide-out {
  from{
    transform: translateX(0px);
  }
  to{
    transform: translateX(1000px);
  }
}
.font-apple{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 15px;
}
.whapichat{
    width:230px;
    user-select:none
}
.new-account-link{
    color: #003965;
    font-weight: 500;
    margin-left: 10px;
    cursor: pointer;
    transition: all .1s ease-in;
}
.new-account-link:hover{
    color: #0061ac;
    text-decoration: underline;
}
.not-account{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 10px;
    font-weight: 300;
}
.google-button{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 215px;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    border: none;
    padding: 6px 12px;
    cursor: pointer;
    user-select: none;
    transition: all .1s ease-in;
    color: #003965;
    font-weight: 500;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.44);
}
.google-button:hover{
    background-color: rgb(142, 182, 228);
    color: #fff;
}
.or{
    color: #003965;
    font-weight: 700;
    margin-top: 15px;
    margin-bottom: 15px;
}
.sign-in-button{
    border: none;
    padding: 6px 24px;
    border-radius: 5px;
    background-color: #E9E9E9;
    width: 215px;
    color: rgb(121, 121, 121);
    font-weight: 500;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.44);
}
.sign-in{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}
.forgot-password-title{
    color: #003965;
    font-size: 12px;
    user-select: none;
    font-weight: 500;
    cursor: pointer;
}
.forgot-password-title:hover{
    color: #0061ac;
    text-decoration: underline;
}
.forgot-password{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
}
/* Inputs --------------------------------------- */
.inputs{
    margin:10px 0;
    position: relative;
}
.input{
    width: 100%;
    padding: 7px 4px;
    border: none;
    border-radius: 5px;
    font-weight: 500;
    background-color: #fff;
}
.input:focus 		{ outline:none; }
.input-label{
    color:rgb(133, 133, 133); 
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:8px;
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
    font-weight: 500;
}
.input:focus ~ .input-label	,.input:valid ~ .input-label	{
  top:-16px;
  font-size:10px;
  color:rgb(133, 133, 133);
  font-weight: normal;
}
.inputs-header{
    color: #003965;
    font-weight: 500;
    margin-bottom: 15px;
}
.login-inputs{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 75px;
}
.login-inputs-main{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px;
}
/* Logo ----------------------------------- */
.user-login-logo{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 50px;
}
/* Login Side ---------------------------------------- */
.login{
    width: 20%;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(212, 222, 230);
    z-index: 999;
}

@media only screen and (max-width: 1611px) {
    .whapichat{
        width:170px;
    }
    .font-apple{
        font-size: 12px;
    }
    .login-inputs-main{
        padding: 5px;
    }
    .input-label{
        left:5px;
        top:6px;
    }
    .forgot-password-title{
        font-size: 10px;
    }
}
@media only screen and (max-width: 1192px) {
    .whapichat{
        width:120px;
    }
    .sign-in-button{
        width: 150px;
    }
    .google-button{
        width: 150px;
    }
    .font-apple{
        font-size: 10px;
    }
    .forgot-password-title{
        font-size: 8px;
    }
    .not-account{
        flex-direction: column;
    }
    .new-account-link{
        margin-left: 0px;
        margin-top: 2px;
    }
    .input:focus ~ .input-label	,.input:valid ~ .input-label	{
        top:-14px;
        font-size:8px;
    }
}
</style>