<template>
    <div class="docs-container">

        <page-header></page-header>

        <div class="docs-main">
            <div class="api-container">
                <div class="api-about-title">
                    METHODS
                </div>
                <div class="methods">
                    <div class="api-column" v-for="(api,index) in apis" :key="api.url">
                        <div :class="api.type === 'POST' ? 'api-box green-lighten' : 'api-box blue-lighten'"
                            @click="api.boxIsOpened = !api.boxIsOpened, jsonBeautify(api.response,index)">
                            <div class="api-type" :class="api.type === 'POST' ? 'green' : 'blue' ">
                                {{api.type}}
                            </div>
                            <div class="api-url">
                                {{api.url}}
                            </div>
                            <div v-if="!api.boxIsOpened" @click.self="api.boxIsOpened = !api.boxIsOpened"
                                class="open-box-icon">
                                <img style="width:2.2vmin;" src="@/assets/icons/chevron-down.svg" alt="">
                            </div>
                            <div v-else @click.self="api.boxIsOpened = !api.boxIsOpened" class="open-box-icon">
                                <img style="width:2.2vmin;" src="@/assets/icons/chevron-up.svg" alt="">
                            </div>
                        </div>
                        <v-expand-transition>
                            <div v-if="api.boxIsOpened">
                                <div
                                    :class="api.type === 'POST' ? 'api-info green-lighten border-top-green' : 'api-info blue-lighten border-top-blue'">
                                    <div class="api-about">
                                        {{api.apiAbout}}
                                    </div>
                                    <div class="api-usage">
                                        <template v-if="api.parameters">
                                            <div
                                                :class="api.type === 'POST' ? 'parameters-title green-text' : 'parameters-title blue-text' ">
                                                Parameters
                                            </div>
                                            <div class="api-usage-table">
                                                <div class="table-header">
                                                    <div class="header-title" style="flex:1 0 10vw;">Paramater</div>
                                                    <div class="header-title" style="flex:2 0 20vw;">Description</div>
                                                    <div class="header-title" style="flex:0 0 10vw;">Data type</div>
                                                </div>
                                                <div class="table-content" v-for="parameter in api.parameters"
                                                    :key="parameter.title">
                                                    <div class="content" style="flex:1 0 10vw;">{{parameter.title}}
                                                    </div>
                                                    <div class="content" style="flex:2 0 20vw;">
                                                        {{parameter.description}}
                                                    </div>
                                                    <div class="content" style="flex:0 0 10vw;">{{parameter.dataType}}
                                                    </div>
                                                </div>
                                            </div>
                                        </template>
                                        <div v-if="api.response"
                                            :class="api.type === 'POST' ? 'response-title green-text' : 'response-title blue-text' ">
                                            Example Response
                                        </div>
                                        <div v-if="api.response" :id="'api-response' + index"
                                            :class="api.type === 'POST' ? 'example-response response-box-green' : 'example-response response-box-blue' ">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </v-expand-transition>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import Vue from "vue"
    import PageHeader from "../components/PageHeader.vue"
    export default {
        data() {
            return {
                apis: [{
                        type: "POST",
                        url: '/send_message/:token',
                        apiAbout: "Send a message to a new or existing chat.",
                        boxIsOpened: false,
                        parameters: [{
                                title: "number",
                                dataType: "string",
                                description: "A phone number starting with the country code. Example; 901234567890"
                            },
                            {
                                title: "message",
                                dataType: "string",
                                description: "Message text."
                            }
                        ],
                        response: {
                            "status": true,
                            "response": {
                                "id": {
                                    "fromMe": true,
                                    "remote": {
                                        "server": "c.us",
                                        "user": "90**********",
                                        "_serialized": "90**********@c.us"
                                    },
                                    "id": "262***************************",
                                    "_serialized": "true_90**********@c.us_262***************************"
                                },
                                "ack": 0,
                                "hasMedia": false,
                                "body": "Hello!!",
                                "type": "chat",
                                "timestamp": 1643800064,
                                "from": "90**********@c.us",
                                "to": "90**********@c.us",
                                "deviceType": "android",
                                "isForwarded": false,
                                "forwardingScore": 0,
