                                "isGif": false,
                                "links": []
                            }
                        }
                    },
                    {
                        type: "GET",
                        url: '/get_message_history/:token/:number/:limit',
                        apiAbout: "Get the message history of the selected contact.",
                        boxIsOpened: false,
                        response: {
                            "id": {
                                "fromMe": false,
                                "remote": "90**********@c.us",
                                "id": "3A******************",
                                "_serialized": "false_90**********@c.us_3A******************"
                            },
                            "ack": 0,
                            "hasMedia": false,
                            "body": "Test",
                            "type": "chat",
                            "timestamp": 1642507257,
                            "from": "90**********@c.us",
                            "to": "90**********@c.us",
                            "deviceType": "ios",
                            "isForwarded": false,
                            "forwardingScore": 0,
                            "isStatus": false,
                            "isStarred": false,
                            "broadcast": false,
                            "fromMe": false,
                            "hasQuotedMsg": false,
                            "vCards": [],
                            "mentionedIds": [],
                            "isGif": false,
                            "links": []
                        }
                    },
                    {
                        type: "GET",
                        url: '/get_contacts/:token',
                        apiAbout: "Get the contact list of the connected client.",
                        boxIsOpened: false,
                        response: {
                            "id": {
                                "server": "c.us",
                                "user": "90**********",
                                "_serialized": "90**********@c.us"
                            },
                            "name": "Test",
                            "isGroup": false,
                            "isReadOnly": false,
                            "unreadCount": 0,
                            "timestamp": 1643177074,
                            "archived": false,
                            "pinned": false,
                            "isMuted": false,
                            "muteExpiration": 0
                        }
                    },
                    {
                        type: "GET",
                        url: '/get_profile_picture/:token',
                        apiAbout: "Get the profile picture of the connected client.",
                        boxIsOpened: false,
                        response: {
                            "status": true,
                            "response": "https://pps.whatsapp.net/v/****************************************************************************************************************************"
                        }

                    },
                    {
                        type: "GET",
                        url: '/get_info/:token',
                        apiAbout: "Get information about the connected device.",
                        boxIsOpened: false,
                        response: {
                            "status": true,
                            "response": {
                                "id": "90**********@c.us",
                                "name": "***",
                                "wa_version": "2.22.2.73",
                                "battery": 76,
                                "platform": "android",
                                "device": {
                                    "os_version": "8.0.0",
                                    "os_build_number": "RNE-L01 8.0.0.351(C432)",
                                    "manufacturer": "HUAWEI",
                                    "model": "HWRNE"
                                }
                            }
                        }
                    },
                    {
                        type: "POST",
                        url: '/send_file/:token',
                        apiAbout: "Send a file to a new or existing chat.",
                        boxIsOpened: false,
                        parameters: [{
                                title: "b64",
                                dataType: "string",
                                description: "Base64-encoded file with mime data, for example data:image/gif;base64,R0lG....."
                            },
                            {
                                title: "fileName",
                                dataType: "String",
                                description: "File name, for example a.png or b.pdf"
                            },
                            {
                                title: "number",
                                dataType: "string",
                                description: "A phone number starting with the country code. Example; 901234567890"
                            },
                            {
                                title: "mimeType",
                                dataType: "string",
                                description: "Media type of file for example application/pdf, image/jpg"
                            }
                        ],
                        response: {
                            "status": true,
                            "response": {
                                "mediaKey": "JNgt+rzJjkJSlJjpopbseWfeZymxpchLmLVytsccyxE=",
                                "id": {
                                    "fromMe": true,
                                    "remote": {
                                        "server": "c.us",
                                        "user": "90**********",
                                        "_serialized": "90**********@c.us"
                                    },
                                    "id": "817**************************************************",
                                    "_serialized": "true_90**********@c.us_817**************************************************"
                                },
                                "ack": 0,
                                "hasMedia": true,
                                "body": "",
                                "type": "image",
                                "timestamp": 1643800205,
                                "from": "90**********@c.us",
                                "to": "90**********@c.us",
                                "deviceType": "android",
                                "isForwarded": false,
                                "forwardingScore": 0,
                                "isStarred": false,
                                "fromMe": true,
                                "hasQuotedMsg": false,
                                "vCards": [],
                                "mentionedIds": [],
                                "isGif": false
                            },
                            "data": {
                                "b64": "iVBORw0.........",
                                "b64Name": "TEST.PNG",
                                "number": "90**********",
                                "type": "image/png"
                            }
                        }
                    }
                ]
            }
        },
        components: {
            PageHeader
        },
        methods: {
            jsonBeautify(object,index) {

                var jsonLine = /^( *)("[\w]+": )?("[^"]*"|[\w.+-]*)?([,[{])?$/mg;
                var replacer = function (match, pIndent, pKey, pVal, pEnd) {
                    var key = '<span style="color: brown">',
                        val = '<span style="color: navy">',
                        str = '<span style="color: olive">',
                        r = pIndent || '';
                    if (pKey)
                        r = r + key + pKey.replace(/[": ]/g, '') + '</span>: ';
                    if (pVal)
                        r = r + (pVal[0] == '"' ? str : val) + pVal + '</span>';
                    return r + (pEnd || '');
                };

                let test = JSON.stringify(object, null, 3)
                    .replace(/&/g, '&amp;').replace(/\\"/g, '&quot;')
                    .replace(/</g, '&lt;').replace(/>/g, '&gt;')
                    .replace(jsonLine, replacer);

                Vue.nextTick(() => {
                    document.getElementById(`api-response${index}`).innerHTML = test;
                })
            }
        }
    }
</script>

<style lang="scss">
    @import "@/scss/mixins.scss";

    .green-text {
        color: rgb(2, 175, 2);
    }

    .blue-text {
        color: var(--whapichat-blue);
    }

    .blue {
        background-color: var(--whapichat-blue)
    }

    .blue-lighten {
        background-color: lighten($color: #305691, $amount: 58)
    }

    .green {
        background-color: rgb(2, 175, 2);
    }

    .green-lighten {
        background-color: lighten($color: rgb(2, 175, 2), $amount: 58)
    }

    .border-top-blue {
        border-top: .1vmin solid #b9cff5;
    }

    .border-top-green {
        border-top: .1vmin solid #bafcba;
    }

    .response-box-green {
        background-color: lighten($color: rgb(2, 175, 2), $amount: 63);
    }

    .response-box-blue {
        background-color: lighten($color: #305691, $amount: 61);
    }

    .docs-container {
        @include d-flex(column, flex-start, stretch);
        width: 100%;
        height: 100%;
        flex: 1 0 1px;
        font-family: var(--whapichat-font);
        gap: 2%;

        .docs-main {
            width: 100%;
            height: 100%;
            padding: 0 1% 1% 1%;

            .api-container {
                @include d-flex(column, flex-start, stretch);
                flex: 1 0 1px;
                width: 100%;
                height: 100%;
                overflow: auto;
                background-color: #fff;
                @include box-shadow;
                border-radius: 3px;
                gap: .8vh;

                .api-about-title {
                    font-weight: 700;
                    flex: 0 0 auto;
                    width: 100%;
                    padding: 1vh .5vw;
                    font-size: 1.5vmin;
                }

                .methods {
                    @include d-flex(column, flex-start, stretch);
                    flex: 1 0 1px;
                    overflow: auto;
                    width: 100%;
                    gap: 2vh;
                    padding: .5vh .5vw;

                    .api-column {
                        @include d-flex(column, flex-start, stretch);
                        width: 100%;
                        border-radius: 2px;
                        @include box-shadow;

                        .api-box {
                            @include d-flex(row, space-between, center);
                            width: 100%;
                            padding: .8%;
                            gap: 1%;

                            .api-type {
                                @include d-flex(column, center, center);
                                flex: 0 0 auto;
                                width: 6vmin;
                                height: 3vmin;
                                color: #fff;
                                border-radius: 3px;
                                font-size: 1.3vmin;
                            }

                            .api-url {
                                @include d-flex(row, flex-start, center);
                                flex: 1 0 1px;
                                height: 3vmin;
                                font-weight: 500;
                                font-size: 1.6vmin;
                            }

                            .open-box-icon {
                                @include d-flex(column, center, center);
                                flex: 0 0 auto;
                                height: 3vmin;
                                cursor: pointer;
                            }
                        }

                        .api-info {
                            @include d-flex(column, flex-start, stretch);
                            gap: 2vmin;
                            padding: .8%;
                            font-size: 1.6vmin;

                            .api-about {
                                font-weight: normal;
                            }

                            .api-usage {

                                @include d-flex(column, flex-start, stretch);
                                gap: 1vmin;

                                .parameters-title {
                                    font-weight: bold;
                                }

                                .api-usage-table {
                                    @include d-flex(column, flex-start, stretch);

                                    .table-header {
                                        @include d-flex(row, space-between, center);
                                        border-bottom: .1vmin solid #ddd;
                                        padding: 1% 0;


                                        .header-title {
                                            @include d-flex(row, flex-start, center);
                                        }
                                    }

                                    .table-content {
                                        @extend .table-header;
                                        padding: 2% 0;


                                        .content {
                                            font-weight: normal;
                                            
                                        }
                                    }
                                }

                                .example-response {
                                    // @include d-flex(columns;, flex-start, stretch);
                                    padding: 1vh 1vw;
                                    border-radius: .1vw;
                                    font-size: 1.5vmin;
                                    @include box-shadow;
                                    word-break: break-all;
                                    word-wrap: break-word;
                                    overflow-wrap: break-word;
                                    white-space: pre-wrap;
                                    user-select: none;
                                }
                            }
                        }
                    }

                    &::-webkit-scrollbar {
                        width: .5vmin;
                        height: .5vmin;
                        border-radius: 10px;
                    }

                    &::-webkit-scrollbar-thumb {
                        background: #aca9a9;
                        border-radius: 10px;
                    }
                }
            }
        }

    }

    @media screen and (device-aspect-ratio: 375/667) {
        .api-container {
            .methods {
                .api-column {
                    margin-bottom: 1vh;

                    .api-box {

                        .api-type {
                            margin-right: 1vw;
                        }
                    }
                }
            }
        }
    }
</style>