<template>
<div>
    <div id="menu" class="menu-main">
        <div class="menu-main-items">
            <div v-for="(item,i) in icons" :key="'icons'+i" :class="(item.api == getSelectedPath.api) ?'selected-closed-menu' :'closed-menu'">
                <div @click="routePage(item)" @mouseenter="hoveredMenuItem = item.value" @mouseleave="hoveredMenuItem = ''" class="closed-menu-items">
                    <div class="closed-menu-items-icon">
                        <img class="icons" :src="require('@/assets/icons/'+item.src)" alt="">
                    </div>
                    <transition name="fade">
                        <div :key="'title'+i" v-if="isOpenMenu && isOpened" class="closed-menu-items-title">
                            {{item.title}}
                        </div>
                    </transition>
                </div>
                <transition name="fade" mode="out-in"> 
                    <div :key="'hover'+i" v-if="hoveredMenuItem == item.value && !isOpenMenu" class="closed-menu-title">
                        {{item.title}}
                    </div>
                </transition>
            </div>
        </div>
        <div id="menu-icon" @click.stop="menuFunc" class="closed-menu-icon">
            <img v-if="!isOpenMenu" class="open-menu-icon" src="@/assets/icons/open-menu.svg" alt="">
            <img v-else src="@/assets/icons/close-menu.svg" class="open-menu-icon" alt="">
        </div>
    </div>
    <div v-if="isOpenMenu" style="width:2.5vw"></div>
</div>
  
</template>

<script>
import {mapMutations,mapGetters} from "vuex"
import {icons} from "@/helpers/enums.js"
import _ from "lodash"
export default {
    data(){
        return{
            icons: _.cloneDeep(icons),
            hoveredMenuItem:"",
            isOpenMenu:false,
            isOpened:false,
        }
    },
    methods:{
        ...mapMutations([
            "setSelectedPath"
        ]),
        routePage(item){
            if(this.getSelectedPath.api != item.api)
                this.$router.push(this.$route.query.redirect|| `${item.api}`);
            let obj = {
                api:item.api,
                title:item.title
            }
            this.setSelectedPath(obj);
        },
        menuFunc(){
            const elMenu = document.getElementById('menu').style;
            if(this.isOpenMenu){
                let id = null;
                let pos = 14;
                let data = this
                this.isOpened = false;
                clearInterval(id);
                id = setInterval(frame, 7);
                function frame() {
                    if (pos == 2.5) {
                        data.isOpenMenu = false;
                        elMenu.position = 'relative'
                        clearInterval(id);
                    } 
                    else {
                        pos-=0.5; 
                        
                        elMenu.width = pos + "vw"; 
                    }
                }
            }
            else{
                this.isOpenMenu = true
                elMenu.position = 'absolute'
                elMenu.zIndex = '999'
                let id = null;
                let pos = 2.5;
                let data = this
                clearInterval(id);
                id = setInterval(frame, 7);
                function frame() {
                    if (pos == 14) {
                        clearInterval(id);
                        data.isOpened = true
                    } 
                    else {
                        pos+=0.5; 
                        elMenu.width = pos + "vw"; 
                    }
                }
            }

        }
    },
    computed:{
        ...mapGetters([
            "getSelectedPath",
        ]),
    },
    mounted(){
      window.onclick = event => {
        if (event.target != document.getElementById("menu") && this.isOpenMenu && this.isOpened){
          const elMenu = document.getElementById('menu').style;
          //console.log("a")
          this.isOpened = false;
          let id = null;
          let pos = 14;
          let data = this
          clearInterval(id);
          id = setInterval(frame, 7);
          function frame() {
              if (pos == 2.5) {
                  data.isOpenMenu = false;
                  elMenu.position = 'relative'
                  clearInterval(id);
              } 
              else {
                  pos-=0.5; 
                  
                  elMenu.width = pos + "vw"; 
              }
          }
        }
      }
      let item={
          api:this.$route.path,
      }
        this.setSelectedPath(item);
    }
}
</script>

<style lang="scss">
@import '@/scss/colors.scss';
@import '@/scss/mixins.scss';

.fade-enter{
    opacity: 0;
}
.fade-enter-active{
    transition: opacity .5s;
}


.menu-main{
    font-size:0.8vw;
    @include d-flex(column,flex-start,center);
    justify-content: space-between!important;
    height: 100%;
    flex: 0 0 auto;
    width: 2.5vw;
    background-color: #003965;
    position: relative;
    .menu-main-items{
        width:100%;
        @include d-flex(column,flex-start,center);
        .closed-menu{
            @include d-flex-center;
            cursor: pointer;
            width: 100%;
            transition: all .15s ease-in;
            position: relative;
            .closed-menu-items{
                @include d-flex(row,flex-start,center);
                margin: 0.43vw 0.43vw 0.43vw 0.43vw;
                width: 100%;
                height: 100%;
                .icons{
                    width: 1.15vw;
                }
                .closed-menu-items-title{
                    @include d-flex(row,flex-start,center);
                    color: #fff;
                    font-weight: normal;
                    margin-left: 0.5vw
                }
                .closed-menu-items-icon{
                    @include d-flex-center;
                    border-radius: 999px;
                    padding: 0.25vw;
                    background-color: #fff;
                }
            }
            .closed-menu-title{
                @include d-flex-center;
                height: 100%;
                font-weight: 500;
                width: 10vw;
                color: #fff;
                position: absolute;
                background-color: var(--menu-hover-color);
                left: 100%;
                z-index: 999;
                border-radius: 0px 4px 4px 0px;
            }
            &:hover{
                background-color: var(--menu-hover-color);
            }
        }
        .selected-closed-menu{
            @extend .closed-menu;
            background-color: var(--menu-hover-color);
        }
    }
    .closed-menu-icon{
        @include d-flex-center;
        width: 100%;
        margin-bottom: 0.8vw;
        padding: 0.25vw;
        cursor: pointer;
        .open-menu-icon{
            width: 1.7vw;
            height: 1.2vw;
        }
        &:hover{
            background-color: var(--menu-hover-color);
        }
    }
    
}
</style>